body,
html {
	margin: 0;
}
.goCenterUI {
	width: 20px;
	height: 20px;
	/* flex-wrap: wrap; */
	position: fixed;
	display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 2px;
	right: 0;
	top: 60%;
	/* flex-grow: 1; */
	overflow: hidden;
	transform: translate(-25%, -50%);
	z-index: 99;
	fill: #444;
    background-color: #fff;
    padding: 10px;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
    cursor: pointer;
}
#goCenterText {
	color: rgb(25, 25, 25);
	font-family: Arial, Helvetica, sans-serif;
}
#goCenterText i {
	color: #666666;
	padding-top: 5px;
	font-size: 25px;
}
#goFcText,
#goPomText,
#goRsText,
#goTiText {
	color: rgb(25, 25, 25);
	font-family: Arial, Helvetica, sans-serif;
	font-size: 15px;
	line-height: 15px;
}
#goFcUI,
#goPomUI,
#goRsUI,
#goTiUI {
	background-color: #fff;
	border: 2px solid #fff;
	border-radius: 3px;
	box-shadow: 0 2px 6px rgba(0, 0, 0, .3);
	cursor: pointer;
	float: left;
	margin-bottom: 22px;
	text-align: center;
	margin-right: 10px;
}
#goFcText i,
#goPomText i,
#goRsText i,
#goTiText i {
	color: #27c896;
	padding-top: 5px;
	font-size: 40px;
	line-height: 50px;
}
#goFcText p,
#goPomText p,
#goRsText p,
#goTiText p {
	color: #666666;
}